html, body {
  font-family: 'Roboto', sans-serif;

}
.demo-selector {
  padding:16px;
  display:flex;
}

.col {
  padding:32px;
}
.col.left {
  width:40%;
}

.col.right {
  width:60%;
}
.col.right .mdc-button__label {
  font-size:12px;
  letter-spacing: initial;
}
.mdc-select {
  width:100%;
}
.row {
  margin-bottom:32px;
}
.code {
  background: #efefef;
  padding:32px;
}
pre {
  position: relative;
  white-space: pre-wrap;
  display:block;
  padding-top: 40px !important;
}
#copyCapture.html:before, #copyCapture.css:before {
  content: "HTML";
  width:100%;
  font-size:12px;
  background:#333;
  padding:8px 16px;
  color:white;
  position:absolute;
  left:0;
  top:0;
  z-index: 999;
  padding-top:10px;
  border-radius: 5px 0 0 5px;
  box-sizing:border-box;
}
#copyCapture.css:before {
  content: "CSS"
}

.clipWrapper button {
  display:flex !important;
  border-radius:5px;
  background:none !important;
  color: #c6e873;
  font-size:12px;
  font-weight:bold;
  cursor:pointer;
  padding:8px !important;
  border:1px solid #797979 !important;
  margin:8px;
}
.clipWrapper button svg {
  display:none;
}
#copyCapture {
  position:relative;
  overflow: hidden;
}
#copyCapture button{
  position:absolute;
  right:16px;
  color:white;
  top:-2px;
  z-index: 10000;
}

.color-palette.card {
  border: 1px solid rgba(0, 0, 0, 0.27);
  padding:16px;
  border-radius:4px;
  display:flex;
  flex-wrap:wrap;
}

.color-palette .swatch {
  height:25px;
  width:25px;
  margin:3px;
  border: 1px solid white;
  cursor:pointer;
  border-radius:3px;
  overflow:hidden;
  position:relative;

}
.swatch:focus, .swatch.selected {
  outline: 0 !important;
  box-shadow: 0 0 0 2px white, 0 0 0 4px blue !important;
}
.swatch:before{
  content:"";
  background-image: url('https://multimedia.3m.com/mws/media/778065P/transparent-graphic-film.jpg');
  height:25px;
  width:25px;
  margin:1px;
  border: 1px solid white;
  cursor:pointer;
  border-radius:4px;
  position:absolute;
  z-index: -1;
  background-size:75px;
  top:0;
  left:0;
}
.color-palette h6 {
  display:block;
}

.seperator {
  border-top:1px solid rgba(0, 0, 0, 0.27);
  padding-top:4px;
  margin-top:4px;
  width:100%;
}
.selection {
  display:block;
  font-size:12px;
  margin-bottom:8px;
  padding-top:8px;
}
.swatch[data-hue="white"] {
  border:1px solid rgba(0, 0, 0, 0.27)
}

.row.menu-icon {
  display:flex;
  align-items: center;
}
.row.menu-icon .MuiAutocomplete-root {
  width:230px;
  margin-left:16px;
}
